@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Geometria";

  font-display: swap;
  src:
    url("../fonts/geometria_bold.otf");
}
@font-face {
  font-style: italic;
  font-weight: 700;
  font-family: "Geometria";

  font-display: swap;
  src:
    url("../fonts/geometria_bolditalic.otf");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Geometria";

  font-display: swap;
  src:
    url("../fonts/geometria_medium.otf");
}
@font-face {
  font-style: italic;
  font-weight: 500;
  font-family: "Geometria";

  font-display: swap;
  src:
    url("../fonts/geometria_mediumitalic.otf");
}
@font-face {
    font-style: normal;
    font-weight: 400;
    font-family: "Geometria";
  
    font-display: swap;
    src:
      url("../fonts/geometria_light.otf");
  }
  @font-face {
    font-style: italic;
    font-weight: 400;
    font-family: "Geometria";
  
    font-display: swap;
    src:
      url("../fonts/geometria_lightitalic.otf");
  }